@import url(https://fonts.googleapis.com/css2?family=Barrio&family=Cabin+Sketch&family=Modak&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  list-style: none;
}

main {
  background-color: #E8DB7D;
  min-height: 100vh;
}

.standard-button {
  margin: 1rem;
  padding: 2rem;
  font-family: "Barrio";
  border-radius: 2em;
  color: #f3f3f3;
  background-color: #BF6900;
}

/* Nav Bar*/

nav {
  height: 4rem;
  background-color: #0d0221;
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  font-family: "Cabin Sketch";
  width: 100%;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}
#navbar {
  margin-right: 3rem;
}

nav > h1 {
  margin-left: 3rem;
}

a {
  color: #f3f3f3;
}

a:visited {
  color: white;
}

/* Landing Page */

#landing-page {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content:space-evenly;
          justify-content:space-evenly;
}

#landing-page > a > button {
  padding: 8rem;
  border-radius: 2rem;
  font-size: 4rem;
  font-family: "Barrio";
  background-color: #26408B;
  color: #f3f3f3;
}

/* Dashboards */

.dashboard {
  font-family: "Cabin Sketch";
  padding-left: 2rem;
  padding-top: 1rem;
}

.dashboard h2, h3, p, li {
  margin: 1rem;
}

.topics-list {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
}

.topic-buttons-container {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  justify-items: center;
}

.completed-trophy {
  font-size: 3.5rem;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

/* Quiz */

#quiz-body {
  padding-left: 2rem;
  font-family: "Cabin Sketch";
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

#quiz-body h2, img, p, button, input, label {
  margin: 1rem;
}

#quiz-body h2 {
  margin-top: 0;
  padding-top: 1rem;
}

#quiz-body img {
  width: 300px;
}

#quiz-body button {
  width: 12rem;
}

p {
    margin:0;
}

#completion-page-wrapper{
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

#trophy-img{
    height: 300px;
}
